

.glitch {
    color: white;
    font-size: 13vw;
    position: relative;
 
    margin: 0 auto;
}

@keyframes noise-anim {
    0% {
        clip: rect(3px, 9999px, 12px, 0);
    }

    5% {
        clip: rect(30px, 9999px, 30px, 0);
    }

    10% {
        clip: rect(91px, 9999px, 84px, 0);
    }

    15% {
        clip: rect(16px, 9999px, 16px, 0);
    }

    20% {
        clip: rect(48px, 9999px, 31px, 0);
    }

    25% {
        clip: rect(72px, 9999px, 95px, 0);
    }

    30% {
        clip: rect(6px, 9999px, 10px, 0);
    }

    35% {
        clip: rect(65px, 9999px, 56px, 0);
    }

    40% {
        clip: rect(68px, 9999px, 40px, 0);
    }

    45% {
        clip: rect(73px, 9999px, 52px, 0);
    }

    50% {
        clip: rect(59px, 9999px, 36px, 0);
    }

    55% {
        clip: rect(78px, 9999px, 67px, 0);
    }

    60% {
        clip: rect(56px, 9999px, 22px, 0);
    }

    65% {
        clip: rect(32px, 9999px, 60px, 0);
    }

    70% {
        clip: rect(53px, 9999px, 40px, 0);
    }

    75% {
        clip: rect(92px, 9999px, 39px, 0);
    }

    80% {
        clip: rect(92px, 9999px, 10px, 0);
    }

    85% {
        clip: rect(82px, 9999px, 44px, 0);
    }

    90% {
        clip: rect(18px, 9999px, 26px, 0);
    }

    95% {
        clip: rect(36px, 9999px, 47px, 0);
    }

    100% {
        clip: rect(69px, 9999px, 70px, 0);
    }
}

.glitch:after {
    content: attr(data-text);
    position: absolute;
    left: 2px;
    text-shadow: -1px 0 red;
    top: 0;
    color: white;
    background: transparent;
    overflow: hidden;
    clip: rect(0, 900px, 0, 0);
    animation: noise-anim 2s infinite linear alternate-reverse;
}

