}
 @media screen and (max-width: 768px){
    .rw-sentence { font-size: 5vw; }
    .form-group {
        margin-bottom: 1px;
    }
    form > button {
        margin-top: 5px !important;
        
    }
    .hide-back{
        position: absolute;
        
        left:1%;
         
      }
      .hide-back:hover{
          background: transparent;
      }
}
@media screen and (max-width: 320px){
    .rw-sentence { font-size: 6vw; }
    .half-component img {
        max-height: 80%;
        max-width: 70%;
        border-radius: 5px;
        transition: ease-out .2s;
      
      }
} 