/* scrollbar yeah */

*::-webkit-scrollbar {
  width: 3px;
  height: 0;
  background-color: rgba(7, 6, 6, 0);
}

*::-webkit-scrollbar-thumb {
  border-radius: 2px;
  -webkit-box-shadow: inset 0 0 2px rgba(20, 20, 20, 0.3);
  background: linear-gradient(30deg,
      rgb(255, 255, 255),
      #117bef,
      rgb(255, 255, 255));
}

* {
  font-family: 'Comfortaa', cursive;

}

html,
body {
  margin: 0;
  padding: 0;
  height: 100%;
  width: 100%;
  overflow: hidden;
}

#root>div {
  background: #49565c !important;
}

.title {
  margin: 20px auto 0;
  text-align: center;
}

.links {
  display: flex;
  flex-direction: column;
  margin-left: 50px;
}

.links .link {
  margin-top: 20px;
}

h2 {
  color: white;
}

.component {
  height: 100%;

  width: 100%;
  background: #49565c;
  display: flex;
  border: none;
  justify-content: center;
  align-items: center;
  position: relative;
}

.component.first-component {
  position: relative;
  flex-direction: column;
  justify-content: space-around;
}

.first-component--inside-scroller {
  position: absolute;
  right: 20px;
  top: 100px;
}

h2 {
  margin: auto;
  text-align: center;
}

.pagination-additional-class {
  margin: 0;
  position: fixed;
  top: 7px;
  font-size: 13px;
  display: flex;
  justify-content: space-around;
  width: 100%;
}

.pagination-additional-class {
  margin: 0;
  padding: 0;
  list-style-type: none;
}

.pagination-additional-class li {
  margin: 0 10px;

  text-align: center;
}

.pagination-additional-class li a {
  color: white !important;
  transition: ease 0.3s;
}

.half-component {
  width: 200vw;
  height: 100%;
  display: flex;
  transition: linear 0.4s;
}

.half-component img {
  max-height: 75%;

  max-width: 85%;
  border-radius: 5px;
  transition: ease-out 0.2s;
}

.half-component img:hover {
  cursor: pointer;
  transition: ease-in 0.2s;
  transform: scale(1.01);
}

.full {
  width: 100%;
  transition: linear 0.4s;
}

.half-visible {
  margin-top: 25px;
  width: 50%;
  height: 95%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-around;
  transition: linear 0.4s;
}

.hide {
  width: 0;
  overflow: hidden;
  opacity: 0;
  transition: linear 0.4s;
}

.half-hidden {
  width: 50%;
  transition: linear 0.4s;
  flex-grow: 1;
  height: 100%;
  opacity: 0;
  display: flex;
  align-items: center;
  justify-content: center;
}

.visible {
  transition: linear 0.4s;
  opacity: 1;
  position: relative;
}

.Toastify__toast-container {
  position: absolute !important;
}

.hide-wrap {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-around;
  position: relative;
}

.hide-back {
  position: absolute;
  bottom: 0;
  left: 0;
  top: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  opacity: 0.5;
  width: 4%;
  font-size: 11px;
  height: 105%;
  padding-bottom: 5px;
  color: wheat;
  transition: linear .3s;
}

.hide-back:hover {
  background: #cccccc55;
  opacity: 1;
  text-shadow: 0 0 2px white;
  cursor: pointer;
  transition: linear .3s;
}

/* form */

.form-wrap {
  width: 100%;
  height: 100%;
  background: #49565c;
  display: flex;
  align-items: center;
  justify-content: center;
}

.hide-viewer {
  width: 75%;
  height: 90%;
  overflow-y: scroll;
  display: flex;
  align-items: center;
  justify-content: space-around;
  flex-direction: column;
  text-align: center;
  padding: 15px;
}

.hide-viewer p {
  color: white;
  line-height: 1.6;
  font-size: 17px;
  letter-spacing: 2px;
  opacity: 0.9;
  transition: linear 0.6s;
}

p.p-hide {
  transition: linear 0.6s;
  opacity: 0;
}

.hide-viewer img {
  box-shadow: 0 0 6px black;
  transition: linear 0.2s;

}

.hide-viewer img:hover {
  transform: none;
  transition: linear 0.2s;

  box-shadow: 0 0 15px black;
}